import { SAT, SATCircle, SATPoint, SATPolygon, SATShape } from 'engine/lib/sat';
import { Engine } from '../Engine';
import { moveAABB } from '../lib/AABB';
import { V2, addV2 } from '../lib/V2';
import { ColliderFacet, ColliderType } from './ColliderFacet';
import { Facet } from './Facet';

const colliderToSATShape = (c: ColliderFacet): SATShape => {
  switch (c.props.type) {
    case ColliderType.Box: {
      const s = new SATPolygon();
      const [x, y] = c.props.offset;
      const [width, height] = c.props.size;
      s.vertices = [
        new SATPoint(x, y),
        new SATPoint(x + width, y),
        new SATPoint(x + width, y + height),
        new SATPoint(x, y + height),
      ];
      s.x = c.entity.transform.position[0];
      s.y = c.entity.transform.position[1];
      return s;
    }
    case ColliderType.Circle: {
      const s = new SATCircle();
      const [x, y] = c.entity.transform.position;
      const [xOffset, yOffset] = c.props.offset;
      s.radius = c.props.radius;
      s.x = x + xOffset + c.props.radius;
      s.y = y + yOffset + c.props.radius;
      return s;
    }
    case ColliderType.Polygon: {
      const s = new SATPolygon();
      const [x, y] = c.entity.transform.position;
      const [xOffset, yOffset] = c.props.offset;
      s.vertices = c.props.points.map(p => new SATPoint(p[0], p[1]));
      s.x = x + xOffset;
      s.y = y + yOffset;
      return s;
    }
  }
};

export class KinematicBodyFacet extends Facet {
  move(amount: V2, engine: Engine) {
    addV2(this.entity.transform.position, amount);
    this.entity.getFacets(ColliderFacet).forEach(ownC => {
      const aabb1 = moveAABB(ownC.aabb, this.entity.transform.position);
      engine.entities.forEach(entity => {
        if (entity === this.entity) return;
        entity.getFacets(ColliderFacet).forEach(c => {
          const aabb2 = moveAABB(c.aabb, entity.transform.position);
          if (
            aabb1.position[0] < aabb2.position[0] + aabb2.size[0]
            && aabb1.position[0] + aabb1.size[0] > aabb2.position[0]
            && aabb1.position[1] < aabb2.position[1] + aabb2.size[1]
            && aabb1.position[1] + aabb1.size[1] > aabb2.position[1]
          ) {
            const collisionInfo = SAT.test(
              colliderToSATShape(ownC),
              colliderToSATShape(c),
            );
            if (collisionInfo) {
              c.collisions.push(ownC);
              ownC.collisions.push(c);
              if (c.props.trigger || ownC.props.trigger) return;
              addV2(this.entity.transform.position, [
                collisionInfo.separation.x * -1,
                collisionInfo.separation.y * -1,
              ]);
            }
          }
        });
      });
    });
  }
}
