import { Entity } from 'engine/Entity';
import { ColliderFacet, ColliderType } from 'engine/facets/ColliderFacet';
import { Facet } from 'engine/facets/Facet';
import { StaticSpriteFacet } from 'engine/facets/StaticSpriteFacet';
import { newV2 } from 'engine/lib/V2';
import { create } from 'engine/lib/create';
import { TreeBottomSprite, TreeShadow, TreeTopSprite } from 'game/sprites/TreeSprite';

interface TreeEntityProps {
  chopped: boolean,
}

export class TreeEntity extends Entity<TreeEntityProps> {

  treeTopSprite: StaticSpriteFacet | null = null;

  createFacets({ chopped }: TreeEntityProps) {
    const treeParts: Facet[] = [
      create(StaticSpriteFacet, { frame: TreeShadow }),
      create(StaticSpriteFacet, { frame: TreeBottomSprite }),
    ];
    if (!chopped) {
      treeParts.push(
        this.treeTopSprite = create(StaticSpriteFacet, { frame: TreeTopSprite }),
      );
    }

    return [
      ...treeParts,
      create(ColliderFacet, { type: ColliderType.Circle, radius: 24, offset: newV2(-13, -40) }),
    ];
  }
}
