import { Engine } from 'engine/Engine';
import { Entity } from 'engine/Entity';
import { Facet } from 'engine/facets/Facet';
import { newV2 } from 'engine/lib/V2';
import { create } from 'engine/lib/create';
import { BotToolChoice, ModuleType } from 'game/state/BotState';
import { RootState } from 'game/state/RootState';
import { MainWorld } from './MainWorld/MainWorld';

const rootState: RootState = {
  player: {
    position: [-585, 154],
    facing: 'right',
    speed: 0.5,
  },
  trees: [
    { chopped: true, position: newV2(-300, -140) },
    { chopped: false, position: newV2(-500, -80) },
    { chopped: false, position: newV2(800, 0) },
    { chopped: false, position: newV2(500, -220) },
    { chopped: false, position: newV2(-1000, 820) },
    { chopped: false, position: newV2(-1500, 520) },
  ],
  bots: [
    {
      id: 'bot-id',
      running: true,
      looping: true,
      cache: {},
      inventory: [{
        type: BotToolChoice.WateringCan,
        count: 2,
      }],
      modules: [
        {
          id: 1,
          type: ModuleType.Cpu,
          position: [-1, 0],
          inputs: {},
          config: null,
        },
      ],
    },
  ],
};

class GameManagerBehavior extends Facet {
  load(newRootState: RootState, engine: Engine) {
    engine.entities = [this.entity];
    engine.add(create(MainWorld, {
      rootState: newRootState,
    }));
  }

  start(engine: Engine) {
    this.load(rootState, engine);
  }
}

export class GameManager extends Entity {
  createFacets() {
    return [
      create(GameManagerBehavior, {}),
    ];
  }
}
