import { Layer } from 'game/components/Layer';
import { LinkButton } from 'game/components/Link';
import { Message, message } from 'game/components/Message';
import { Modal, ModalBody } from 'game/components/Modal';
import { FEEDBACK_URL } from 'game/config';
import { memo } from 'react';

interface SuccessModalProps {
  onClose: () => void,
}

export const SuccessModal: React.FC<SuccessModalProps> = memo(({ onClose }) => {
  return (
    <Layer>
      <Modal heading={message('Congratulations!')} onClose={onClose}>
        <ModalBody>
          <p>
            <Message
              text="You did it! That's all for now. Stay tuned for more updates. In the meantime, you can {reload} to try out another solution or send me {feedback}."
              values={{
                reload: (
                  <LinkButton onClick={() => window.location.reload()}>
                    <Message text="reload" />
                  </LinkButton>
                ),
                feedback: (
                  <a href={FEEDBACK_URL} target="_blank" rel="noreferrer">
                    <Message text="feedback" />
                  </a>
                ),
              }}
            />
          </p>
          <p><Message text="Thanks for playing!" /></p>
        </ModalBody>
      </Modal>
    </Layer>
  );
});
