import { Entity } from 'engine/Entity';
import { getDistance } from 'engine/lib/V2';
import { PlantEntity } from 'game/entities/PlantEntity';
import { PondEntity } from 'game/entities/PondEntity';
import { TreeEntity } from 'game/entities/TreeEntity';
import { NearbyModule, NearbySelection, determineNearbySelection } from 'game/state/BotState';
import { Processor } from '../Processor';

export const nearbyProcessor: Processor<NearbyModule> = (module, ctx) => {
  const { engine } = ctx;
  const { modules } = ctx.bot.props.state;
  let matches: Entity[] = [];
  let selection = module.config.manualSelection;
  if (module.inputs.item) {
    const itemModule = modules.find(m => m.id === module.inputs.item?.moduleId);
    if (itemModule) {
      const itemResult = ctx.processOther(itemModule, ctx);
      if (itemResult.status !== 'success') {
        return { status: 'null' };
      }
      selection = determineNearbySelection(itemModule, ctx.bot.props.state);
    }
  }
  switch (selection) {
    case NearbySelection.DryPlant: {
      matches = engine.entities.filter(e => e instanceof PlantEntity && e.props.waterAmount < 1);
      break;
    }
    case NearbySelection.Tree: {
      matches = engine.entities.filter(e => e instanceof TreeEntity && !e.props.chopped);
      break;
    }
    case NearbySelection.Water: {
      matches = engine.entities.filter(e => e instanceof PondEntity);
    }
  }
  const { position } = ctx.bot.entity.transform;
  const [closest] = matches.sort((a, b) => (
    getDistance(position, a.transform.position) - getDistance(position, b.transform.position)
  ));
  if (closest) {
    return { status: 'success', moduleId: module.id, value: { type: 'entity', entity: closest } };
  }
  return { status: 'null' };
};
