import { Engine } from 'engine/Engine';
import { Entity } from 'engine/Entity';
import { Transform } from 'engine/Transform';
import { Facet } from 'engine/facets/Facet';
import { StaticSpriteFacet } from 'engine/facets/StaticSpriteFacet';
import { addV2 } from 'engine/lib/V2';
import { create } from 'engine/lib/create';
import { Activatable } from 'game/behaviors/Activatable';
import { DirtSprite } from 'game/sprites/DirtSprite';
import { MainWorldSprite } from 'game/sprites/MainWorldSprite';
import { RootState } from 'game/state/RootState';
import { prefsStorage } from 'game/tutorial/prefs';
import times from 'lodash/times';
import { BotEntity } from '../BotEntity';
import { PlantEntity } from '../PlantEntity';
import { PlayerEntity } from '../PlayerEntity';
import { PondEntity } from '../PondEntity';
import { TreeEntity } from '../TreeEntity';
import { MainWorldUI } from './MainWorldUI';

const strutPath = require('game/sfx/San_Jose_Strut.mp3');
const botClosePath = require('game/sfx/bot-close.mp3');
const botOpenPath = require('game/sfx/bot-open.mp3');

interface MainWorldProps {
  rootState: RootState,
}

export class MainWorldBehavior extends Facet<MainWorldProps> {

  ui: MainWorldUI | null = null;
  player: PlayerEntity | null = null;
  editingBot: BotEntity | null = null;
  successModalShown = false;

  strutSound = new Audio(strutPath);
  botOpenSound = new Audio(botOpenPath);
  botCloseSound = new Audio(botClosePath);

  start(engine: Engine) {
    this.botOpenSound.volume = 0.5;
    this.botCloseSound.volume = 0.5;
    const { rootState } = this.props;

    engine.camera.scale = 0.5;

    this.player = engine.add(create(PlayerEntity, { playerState: rootState.player }));
    this.ui = engine.add(create(MainWorldUI, { bot: null, showSuccessModal: false }));

    engine.add(create(PondEntity, {}), { transform: new Transform({ position: [200, 100] }) });

    let plants: PlantEntity[];
    const onWatered = () => {
      if (this.successModalShown || prefsStorage.get().disableHelp) return;
      if (this.ui && plants.every(plant => plant.props.waterAmount === 1)) {
        this.strutSound.play();
        this.ui.props.showSuccessModal = true;
        this.successModalShown = true;
      }
    };
    plants = times(3, i => {
      return engine.add(create(PlantEntity, { waterAmount: 0, onWatered }), {
        transform: new Transform({ position: addV2([45 + i * 90, 45], [-400, 100]) }),
      });
    });
    const botEntity = engine.add(create(BotEntity, {
      state: rootState.bots[0],
      editBot: () => {
        this.editingBot = botEntity;
        botEntity.getFacet(Activatable)?.disable(true);
        this.botOpenSound.play();
      },
    }));

    rootState.trees.forEach(({ chopped, position }) => {
      engine.add(create(TreeEntity, { chopped }), { transform: new Transform({ position }) });
    });
  }

  update(delta: number, { input, camera }: Engine): void {
    camera.position = addV2([0, -80], this.props.rootState.player.position);

    if (this.editingBot && (input.keyDown.Escape || input.keyDown.KeyE)) {
      // TODO: This is essentially like a stopPropagation() so that the BotEntity
      // doesn't reopen the "edit bot" screen in the same frame. There should be
      // a less hacky way to do it.
      delete input.keyDown.KeyE;
      this.editingBot?.getFacet(Activatable)?.disable(false);
      this.editingBot = null;
      this.botCloseSound.play();
    }

    if (this.ui) this.ui.props.bot = this.editingBot?.props.state || null;
  }
}

export class MainWorld extends Entity<MainWorldProps> {
  zIndex = -1;
  createFacets(props: MainWorldProps) {
    return [
      create(MainWorldBehavior, props),
      create(StaticSpriteFacet, { frame: MainWorldSprite }),
      create(StaticSpriteFacet, { frame: DirtSprite, offset: [-400, 100] }),
    ];
  }
}
