import { Entity } from 'engine/Entity';
import { V2 } from 'engine/lib/V2';

export enum ModuleType {
  Cpu = 'Cpu',
  Use = 'Use',
  Nearby = 'Nearby',
  Inventory = 'Inventory',
  Logic = 'Logic',
}

export const MODULE_UNIT_SIZE = 200;

export const ModuleSize: Record<ModuleType, V2> = {
  [ModuleType.Cpu]: [1, 3],
  [ModuleType.Use]: [1, 3],
  [ModuleType.Nearby]: [1, 3],
  [ModuleType.Inventory]: [1.5, 3],
  [ModuleType.Logic]: [1, 3],
};

export interface WireSource {
  moduleId: number,
  socketName: string,
}

export interface ModuleBase<T, TInputs extends string, TConfig = null> {
  type: T,
  id: number,
  position: V2,
  inputs: Partial<Record<TInputs, WireSource>>,
  config: TConfig,
}

export type AnyModule = ModuleBase<ModuleType, string, unknown>;

export interface CpuModule extends ModuleBase<ModuleType.Cpu, 'cpu'> {
}

export enum BotToolChoice {
  WateringCan = 'WateringCan',
  Axe = 'Axe',
}

export const WATERING_CAN_MAX = 10;

export type UseModule = ModuleBase<ModuleType.Use, 'target', {
  tool: null | BotToolChoice, // null = 'auto'
}>;

export enum NearbySelection {
  DryPlant = 'DryPlant',
  Water = 'Water',
  Tree = 'Tree',
  RedHerring = 'RedHerring',
}

export type NearbyModule = ModuleBase<ModuleType.Nearby, 'item', {
  manualSelection: null | NearbySelection,
}>;

export enum InventoryOperation {
  Lacks = 'Lacks',
  Has = 'Has',
}

export enum InventorySelection {
  Water = 'Water',
  RedHerring = 'RedHerring',
}

export type InventoryModule = ModuleBase<ModuleType.Inventory, never, {
  operation: InventoryOperation,
  selection: null | InventorySelection,
}>;

export interface LogicModule extends ModuleBase<ModuleType.Logic, '0' | '1' | '2' | '3' | '4'> {
  operation: 'fallback' | 'sequence',
}

export type Module =
  | CpuModule
  | UseModule
  | NearbyModule
  | InventoryModule
  | LogicModule;

export type ModuleMap = {
  [T in ModuleType]: Extract<Module, { type: T }>;
};

// TODO: Probably need to use entityId instead of Entity to make this serializable
export type ProcessPayload =
  | { type: 'command', action: 'use', tool: BotToolChoice, target: Entity, enRoute: boolean }
  | { type: 'item', item: InventoryItem }
  | { type: 'entity', entity: Entity };

interface MissingError {
  type: 'missing',
  item: BotToolChoice | InventorySelection,
}

interface UnclearError {
  type: 'unclear',
}

export type ProcessError = MissingError | UnclearError/* | OtherError */;

export type ProcessStatus =
  | { status: 'null' }
  | { status: 'running', value: ProcessPayload }
  | { status: 'error', error: ProcessError }
  | { status: 'success', moduleId: number, value: ProcessPayload };

export interface InventoryItem {
  type: BotToolChoice,
  count: number,
}

type CacheResult = {
  result: ProcessStatus,
  inputs: Partial<{ [socketName: string]: CacheResult }>,
  config: unknown,
};

export interface BotState {
  id: string,
  running: boolean,
  looping: boolean,
  modules: Module[],
  cache: Partial<{ [moduleId: string]: CacheResult }>,
  inventory: InventoryItem[],
}

export enum SocketType {
  Item = 0b001,
  Instance = 0b010,
  Command = 0b100,
}

const AnySocketType: SocketType = SocketType.Item | SocketType.Instance | SocketType.Command;

export interface SocketDef {
  type: SocketType,
  direction: 'in' | 'out',
  position: V2,
}

const type = <T>(t: T): T => t;

