export const determineNearbySelection = (module: Module, bot: BotState): NearbySelection | null => {
  switch (module.type) {
    case ModuleType.Nearby: {
      if (module.inputs.item) {
        const itemModule = bot.modules.find(m => m.id === module.inputs.item?.moduleId);
        if (itemModule) return determineNearbySelection(itemModule, bot);
      }
      return module.config.manualSelection;
    }
    case ModuleType.Inventory: {
      switch (module.config.selection) {
        case InventorySelection.RedHerring: return NearbySelection.RedHerring;
        case InventorySelection.Water: return NearbySelection.Water;
        case null: return null;
      }
    }
    // eslint-disable-next-line no-fallthrough
    case ModuleType.Logic: {
      const result = bot.cache[module.id]?.result;
      if (result?.status === 'success') {
        const sourceModule = bot.modules.find(m => m.id === result.moduleId);
        if (sourceModule) return determineNearbySelection(sourceModule, bot);
      }
    }
  }
  return null;
};

export const determineTool = (module: Module, bot: BotState): BotToolChoice | null => {
  switch (module.type) {
    case ModuleType.Use: {
      const targetModuleId = module.inputs.target?.moduleId;
      const targetModule = targetModuleId && bot.modules.find(m => m.id === targetModuleId);
      if (!targetModule) return null;
      return determineTool(targetModule, bot);
    }
    case ModuleType.Nearby: {
      const selection = determineNearbySelection(module, bot);
      switch (selection) {
        case NearbySelection.DryPlant: return BotToolChoice.WateringCan;
        case NearbySelection.Water: return BotToolChoice.WateringCan;
        case NearbySelection.Tree: return BotToolChoice.Axe;
      }
      return null;
    }
    case ModuleType.Logic: {
      const result = bot.cache[module.id]?.result;
      if (result?.status === 'success') {
        const sourceModule = bot.modules.find(m => m.id === result.moduleId);
        if (sourceModule) return determineTool(sourceModule, bot);
      }
    }
  }
  return null;
};
