const messages: Record<NearbySelection, string> = {
  [NearbySelection.DryPlant]: 'Unwatered plant',
  [NearbySelection.RedHerring]: 'Red herring',
  [NearbySelection.Tree]: 'Tree',
  [NearbySelection.Water]: 'Water source',
};

const styles = createStyles({
  select: {
    WebkitAppearance: 'none',
    width: '100%',
    height: '100%',
    cursor: 'pointer',
  },
});

const renderIcon = (selection: NearbySelection | null): JSX.Element | null => {
  switch (selection) {
    case NearbySelection.DryPlant: return <DryPlantIcon />;
    case NearbySelection.Tree: return <TreeIcon />;
    case NearbySelection.Water: return <WaterIcon />;
    case NearbySelection.RedHerring: return <HerringIcon />;
    case null: return null;
  }
};

interface ButtonProps {
  active: boolean,
}

const Button: React.FC<ButtonProps> = ({ active }) => (
  <g>
    {active ? (
      <>
        <rect fill="#8C8A83" stroke="#92856D" strokeWidth={2} width={80} height={42} rx={2} y={4} />
        <rect fill="#FFFFFF" stroke="#CCC5BC" strokeWidth={2} width={80} height={42} rx={2} filter={`url(#${FILTER_GLOW_BEIGE_ID})`} />
      </>
    ) : (
      <>
        <rect fill="#CCCAC2" stroke="#92856D" strokeWidth={2} width={80} height={42} rx={2} y={4} />
        <rect fill="#CCCAC2" stroke="#92856D" strokeWidth={2} width={80} height={42} rx={2} />
      </>
    )}
  </g>
);

