/* eslint-disable react-hooks/rules-of-hooks */
import { Engine } from 'engine/Engine';
import { Entity } from 'engine/Entity';
import { ColliderFacet, ColliderType } from 'engine/facets/ColliderFacet';
import { Facet } from 'engine/facets/Facet';
import { KinematicBodyFacet } from 'engine/facets/KinematicBodyFacet';
import { SpriteFacet } from 'engine/facets/SpriteFacet';
import { SpriteGroupFacet } from 'engine/facets/SpriteGroupFacet';
import { StaticSpriteFacet } from 'engine/facets/StaticSpriteFacet';
import { emptyV2 } from 'engine/lib/V2';
import { create } from 'engine/lib/create';
import { svgText } from 'engine/lib/svgText';
import { Activatable } from 'game/behaviors/Activatable';
import { FILTER_OUTLINE_ID } from 'game/defs/FilterOutline';
import { fastMapValues } from 'game/lib/fastMapValues';
import { shallowEqual } from 'game/lib/shallowEqual';
import { Processor, ProcessorContext } from 'game/modules/Processor';
import { cpuProcessor } from 'game/modules/cpu/cpuProcessor';
import { inventoryProcessor } from 'game/modules/inventory/inventoryProcessor';
import { logicProcessor } from 'game/modules/logic/logicProcessor';
import { nearbyProcessor } from 'game/modules/nearby/nearbyProcessor';
import { useProcessor } from 'game/modules/use/useProcessor';
import { BotArmsSprite, BotArmsWateringSprite } from 'game/sprites/BotArmsSprite';
import { BotEyesClosedSprite, BotEyesOpenSprite } from 'game/sprites/BotEyesSprite';
import { BotShadow, BotSprite } from 'game/sprites/BotSprite';
import { AnyModule, BotState, Module, ModuleType, ProcessStatus } from 'game/state/BotState';
import { BlinkerBehavior } from './bot/BlinkerBehavior';
import { BotTooltipBehavior } from './bot/BotTooltipBehavior';

const errorSoundPath = require('game/sfx/error.mp3');
const waterSoundPath = require('game/sfx/splash.mp3');

const errorSound = new Audio(errorSoundPath);

type AudioManagerOptions = Partial<Pick<HTMLMediaElement, 'src' | 'loop'>>;

class AudioManager {

  playing = false;
  audio: HTMLAudioElement;

  constructor(options: AudioManagerOptions) {
    this.audio = new Audio();
    Object.assign(this.audio, options);
  }

  play() {
    if (this.playing) return;
    this.playing = true;
    this.audio.play();
  }
  pause() {
    if (!this.playing) return;
    this.playing = false;
    this.audio.pause();
  }
}

const processModule: Processor<Module> = (m, ctx) => {
  switch (m.type) {
    case ModuleType.Cpu: return cpuProcessor(m, ctx);
    case ModuleType.Inventory: return inventoryProcessor(m, ctx);
    case ModuleType.Logic: return logicProcessor(m, ctx);
    case ModuleType.Nearby: return nearbyProcessor(m, ctx);
    case ModuleType.Use: return useProcessor(m, ctx);
  }
};

interface BotBehaviorProps {
  sprite: SpriteGroupFacet,
  armsSprite: StaticSpriteFacet,
  state: BotState,
  editBot: () => void,
}

export class BotBehavior extends Facet<BotBehaviorProps> {

  tooltip = create(BotTooltipBehavior, { result: null });
  activatable: Activatable | null = null;
  result: ProcessStatus | null = null;
  lastPosition = emptyV2;
  lastRunning = false;
  lastResult: ProcessStatus | null = null;

  waterAudio = new AudioManager({ src: waterSoundPath, loop: true });

  resolveInputs(module: AnyModule) {
    const { cache } = this.props.state;
    return fastMapValues(module.inputs, wire => cache[wire.moduleId]);
  }

  removeCachedResult(module: Module, recursive = false) {
    const { cache } = this.props.state;
    delete cache[module.id];
    if (!recursive) return;
    Object.values(module.inputs).forEach(wire => {
      const otherModule = this.props.state.modules.find(m => m.id === wire.moduleId);
      if (otherModule) this.removeCachedResult(otherModule);
    });
  }

  removeInvalidCache(module: Module) {
    const { cache } = this.props.state;
    Object.values(module.inputs).forEach(wire => {
      const otherModule = this.props.state.modules.find(m => m.id === wire.moduleId);
      if (otherModule) this.removeInvalidCache(otherModule);
    });
    const cachedResult = cache[module.id];
    if (!cachedResult) return;
    if (
      !shallowEqual(cachedResult.inputs, this.resolveInputs(module))
      || !shallowEqual(cachedResult.config, module.config)
    ) {
      const recursive = module.type === ModuleType.Logic;
      this.removeCachedResult(module, recursive);
    }
  }

  processModule(module: Module, delta: number, engine: Engine): ProcessStatus {
    const processorContext: ProcessorContext = {
      bot: this,
      delta,
      engine,
      processOther: (m, ctx) => {
        const cachedResult = this.props.state.cache[m.id];
        if (cachedResult) {
          if (cachedResult.result.status === 'success' || cachedResult.result.status === 'error') {
            return cachedResult.result;
          }
        }
        const result = processModule(m, ctx);
        this.props.state.cache[m.id] = {
          result,
          inputs: this.resolveInputs(m),
          config: m.config ? { ...m.config } : null,
        };
        return result;
      },
    };

    return processorContext.processOther(module, processorContext);
  }

  start(engine: Engine) {
    this.entity.addFacet(this.tooltip, engine);
    const { sprite } = this.props;
    let text: StaticSpriteFacet | null = null;
    this.activatable = this.entity.addFacet(
      create(Activatable, {
        activate: active => {
          sprite.props.filter = active ? `url(#${FILTER_OUTLINE_ID})` : undefined;
          if (active) {
            text = create(StaticSpriteFacet, {
              frame: svgText({
                style: {
                  fontSize: '2rem',
                  fontWeight: 'bold',
                  fill: 'yellow',
                  stroke: 'black',
                  strokeWidth: 10,
                  paintOrder: 'stroke',
                  textAnchor: 'middle',
                },
                x: 64,
                y: -210,
                children: '[e] Edit',
              }),
            });
            this.entity.addFacet(text, engine);
          } else if (text) {
            this.entity.removeFacet(text);
          }
        },
      }),
      engine,
    );
  }

  update(delta: number, engine: Engine): void {
    if (engine.input.keyDown.KeyE && this.activatable?.active) {
      this.activatable.activate(false);
      this.props.editBot();
    }
    const { result } = this;
    this.tooltip.props.result = result;
    if (
      result?.status === 'running'
      && result.value.type === 'command'
      && !result.value.enRoute
    ) {
      this.props.armsSprite.props.frame = BotArmsWateringSprite;
      this.props.sprite.transform.rotation = -8;
      this.waterAudio.play();
    } else {
      this.props.armsSprite.props.frame = BotArmsSprite;
      this.props.sprite.transform.rotation = 0;
      this.waterAudio.pause();
    }
    if (this.lastPosition[0] > this.entity.transform.position[0]) {
      this.props.sprite.transform.scale[0] = 1;
    } else if (this.lastPosition[0] < this.entity.transform.position[0]) {
      this.props.sprite.transform.scale[0] = -1;
    }
    this.lastPosition = [...this.entity.transform.position];
    if (this.lastRunning && !this.props.state.running) {
      this.props.state.cache = {};
    }
    this.lastRunning = this.props.state.running;

    if (this.result?.status === 'error' && this.lastResult?.status !== 'error') {
      errorSound.play();
    }
  }

  physicsUpdate(delta: number, engine: Engine): void {
    if (!engine.time.timeScale) return;
    const { state } = this.props;
    if (!state.running) return;
    const cpu = state.modules.find(m => m.type === ModuleType.Cpu);
    if (!cpu) return;

    this.removeInvalidCache(cpu);

    this.lastResult = this.result;
    this.result = this.processModule(cpu, delta, engine);
    (window as any).result = this.result;
    if (this.result.status !== 'running') {
      this.props.state.cache = {};
      if (!this.props.state.looping) {
        this.props.state.running = false;
      }
    }
  }
}

interface BotEntityProps {
  editBot: () => void,
  state: BotState,
}

export class BotEntity extends Entity<BotEntityProps> {
  createFacets(props: BotEntityProps) {
    const eyesContainer = create(StaticSpriteFacet, { frame: null, offset: [31, -154] });
    const armsSprite = create(StaticSpriteFacet, { frame: BotArmsSprite, offset: [27, -104] });
    const sprite = create(SpriteGroupFacet, {
      children: [
        create(SpriteFacet, { frameset: BotSprite.down, offset: [0, -186] }),
        eyesContainer,
        armsSprite,
      ],
    });

    return [
      create(BotBehavior, {
        sprite,
        armsSprite,
        editBot: props.editBot,
        state: props.state,
      }),
      create(BlinkerBehavior, {
        frameOpen: BotEyesOpenSprite,
        frameOpenRange: [1000, 5000],
        frameClosed: BotEyesClosedSprite,
        frameClosedRange: [500, 1000],
        sprite: eyesContainer,
      }),
      create(StaticSpriteFacet, { frame: BotShadow, offset: [0, -186] }),
      sprite,
      create(ColliderFacet, { type: ColliderType.Box, offset: [14, -12], size: [100, 28] }),
      create(ColliderFacet, { type: ColliderType.Circle, offset: [0, -12], radius: 14 }),
      create(ColliderFacet, { type: ColliderType.Circle, offset: [100, -12], radius: 14 }),
      create(KinematicBodyFacet, {}),
    ];
  }
}
