import { KinematicBodyFacet } from 'engine/facets/KinematicBodyFacet';
import { getDistance, newV2, scaleV2 } from 'engine/lib/V2';
import { PlantEntity } from 'game/entities/PlantEntity';
import { PondEntity } from 'game/entities/PondEntity';
import { TreeEntity } from 'game/entities/TreeEntity';
import { BotToolChoice, InventorySelection, ProcessError, ProcessPayload, ProcessStatus, UseModule, WATERING_CAN_MAX, determineTool } from 'game/state/BotState';
import clamp from 'lodash/clamp';
import round from 'lodash/round';
import { Processor } from '../Processor';

const err = (error: ProcessError): ProcessStatus => ({
  status: 'error',
  error,
});

// TODO: Organize this better
// eslint-disable-next-line complexity
export const useProcessor: Processor<UseModule> = (module, ctx) => {
  const { target } = module.inputs;
  const { modules, inventory } = ctx.bot.props.state;
  const targetModule = target && modules.find(m => m.id === target.moduleId);
  if (!targetModule) return { status: 'null' };
  const targetResult = ctx.processOther(targetModule, ctx);
  if (targetResult.status !== 'success' || targetResult.value.type !== 'entity') {
    return { status: 'null' };
  }
  const tool = module.config.tool || determineTool(module, ctx.bot.props.state);
  if (!tool) return { status: 'null' };
  const body = ctx.bot.entity.getFacet(KinematicBodyFacet);
  if (!body) return { status: 'null' };

  const { position } = ctx.bot.entity.transform;
  const inventoryTool = inventory.find(item => item.type === tool);
  if (!inventoryTool) {
    return err({ type: 'missing', item: tool });
  }

  const payload: ProcessPayload = {
    type: 'command',
    action: 'use',
    target: targetResult.value.entity,
    tool,
    enRoute: true,
  };

  // TODO: Use an enum so unhandled cases are caught at compile time
  if (targetResult.value.entity instanceof PlantEntity) {
    if (inventoryTool.type !== BotToolChoice.WateringCan) {
      return err({ type: 'unclear' });
    }
    const d = getDistance(position, targetResult.value.entity.transform.position);
    const amountNeeded = round(1 - targetResult.value.entity.props.waterAmount, 2);
    if (inventoryTool.count < amountNeeded) {
      // TODO: Decouple concept of "water" from inventory
      return err({ type: 'missing', item: InventorySelection.Water });
    }
    if (d <= 10) {
      payload.enRoute = false;
      const amountToGive = round(clamp(ctx.delta / 1100, amountNeeded), 2);
      inventoryTool.count = round(inventoryTool.count - amountToGive, 2);
      const complete = targetResult.value.entity.water(amountToGive);
      if (complete) {
        return { status: 'success', value: payload, moduleId: module.id };
      }
      return { status: 'running', value: payload };
    }
  }
  if (targetResult.value.entity instanceof PondEntity) {
    if (inventoryTool.type !== BotToolChoice.WateringCan) {
      return err({ type: 'unclear' });
    }
    const d = getDistance(position, targetResult.value.entity.transform.position);
    if (d < 20) {
      payload.enRoute = false;
      const wateringCan = inventory.find(item => item.type === BotToolChoice.WateringCan);
      if (wateringCan) {
        const amount = (ctx.delta / 1000) * 7;
        wateringCan.count = clamp(wateringCan.count + amount, WATERING_CAN_MAX);
        if (wateringCan.count === WATERING_CAN_MAX) {
          return { status: 'success', moduleId: module.id, value: payload };
        }
      }
      return { status: 'running', value: payload };
    }
  }
  if (targetResult.value.entity instanceof TreeEntity) {
    if (inventoryTool.type !== BotToolChoice.Axe) {
      return err({ type: 'unclear' });
    }
  }

  const [x1, y1] = ctx.bot.entity.transform.position;
  const [x2, y2] = targetResult.value.entity.transform.position;
  const angle = Math.atan2(y2 - y1, x2 - x1);
  const v = newV2(Math.cos(angle), Math.sin(angle));
  const botSpeed = 0.15;
  body.move(scaleV2(v, botSpeed * ctx.delta), ctx.engine);
  return { status: 'running', value: payload };
};
