import { AxeIcon } from './icons/AxeIcon';
import { WateringCanIcon } from './icons/WateringCanIcon';
import { moduleStyles } from './moduleStyles';

const advanceTool = (module: UseModule, howMany: number) => {
  const choices = Object.values(BotToolChoice);
  const i = module.config.tool ? choices.indexOf(module.config.tool) : -1;
  const choiceCount = choices.length + 1;
  const nextChoice = choices[(i + howMany + choiceCount) % choiceCount] || null;
  module.config.tool = nextChoice;
};

const renderTool = (tool: BotToolChoice) => {
  switch (tool) {
    case BotToolChoice.WateringCan: return <WateringCanIcon />;
    case BotToolChoice.Axe: return <AxeIcon />;
  }
};

