import { Engine } from 'engine/Engine';
import { Entity } from 'engine/Entity';
import { ColliderFacet, ColliderType } from 'engine/facets/ColliderFacet';
import { Facet } from 'engine/facets/Facet';
import { KinematicBodyFacet } from 'engine/facets/KinematicBodyFacet';
import { SpriteFacet } from 'engine/facets/SpriteFacet';
import { StaticSpriteFacet } from 'engine/facets/StaticSpriteFacet';
import { emptyV2, isSameV2, newV2, scaleV2 } from 'engine/lib/V2';
import { create } from 'engine/lib/create';
import { Activatable } from 'game/behaviors/Activatable';
import { PlayerShadow, PlayerSprite } from 'game/sprites/PlayerSprite';
import { PlayerState } from 'game/state/PlayerState';

interface PlayerBehaviorProps {
  playerState: PlayerState,
  activatableCollider: ColliderFacet,
}

export class PlayerBehavior extends Facet<PlayerBehaviorProps> {

  activated: Activatable | undefined = undefined;

  start() {
    this.entity.transform.position = this.props.playerState.position;
  }

  update(delta: number, { input, time }: Engine) {
    if (!time.timeScale) return;
    const axes = input.getAxes();
    const sprite = this.entity.getFacet(SpriteFacet);
    if (!sprite) return;
    if (isSameV2(axes, emptyV2)) {
      sprite.set(PlayerSprite.idle);
    } else {
      sprite.set(PlayerSprite.running);
      if (axes[0] > 0) {
        this.props.playerState.facing = 'right';
      } else if (axes[0] < 0) {
        this.props.playerState.facing = 'left';
      }
    }
    const { facing } = this.props.playerState;
    this.entity.transform.scale[0] = facing === 'right' ? 1 : -1;

    let activatable: Activatable | undefined;
    this.props.activatableCollider.collisions.some(c => {
      activatable = c.entity.getFacet(Activatable);
      if (activatable?.disabled) { activatable = undefined; }
      return activatable;
    });

    if (this.activated !== activatable) {
      this.activated?.activate(false);
      this.activated = activatable;
      activatable?.activate(true);
    }
  }

  physicsUpdate(delta: number, engine: Engine) {
    const axes = engine.input.getAxes();
    const body = this.entity.getFacet(KinematicBodyFacet);
    if (!body) return;
    body.move(scaleV2(axes, this.props.playerState.speed * delta), engine);
  }
}

interface PlayerEntityProps {
  playerState: PlayerState,
}

export class PlayerEntity extends Entity<PlayerEntityProps> {
  createFacets({ playerState }: PlayerEntityProps) {
    const activatableCollider = create(
      ColliderFacet,
      { type: ColliderType.Box, offset: [-120, -80], size: [240, 140], trigger: true },
    );
    return [
      create(SpriteFacet, { frameset: PlayerSprite.idle, offset: newV2(-80, -230) }),
      create(StaticSpriteFacet, { frame: PlayerShadow }),
      create(KinematicBodyFacet, {}),
      create(PlayerBehavior, { playerState, activatableCollider }),
      activatableCollider,
      create(ColliderFacet, { type: ColliderType.Box, size: [50, 20], offset: [-25, -20] }),
      create(ColliderFacet, { type: ColliderType.Circle, radius: 10, offset: [15, -20] }),
      create(ColliderFacet, { type: ColliderType.Circle, radius: 10, offset: [-35, -20] }),
    ];
  }
}
